/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { sentenceCase, capitalCase  } from 'change-case';
import { useState, useEffect, } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { useSnackbar } from 'notistack';

// @mui
import { useTheme } from '@mui/material/styles';
import {
  Card,
  Table,
  Button,
  Checkbox,
  TableRow,
  TableBody,
  TableCell,
  Container,
  Typography,
  TableContainer,
  TablePagination,
  Switch,
  Tabs,
  Tab,
} from '@mui/material';
// swal 
import swal from 'sweetalert';
// format
import FormControlLabel from '@mui/material/FormControlLabel';
// routes
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Iconify from '../../components/Iconify';
import Scrollbar from '../../components/Scrollbar';
import SearchNotFound from '../../components/SearchNotFound';

import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// sections
import { UserListHead, PlanToolbar, PlanMoreMenu } from '../../sections/admin/user/list';


// axios

import axios from '../../utils/axios'; 

// ----------------------------------------------------------------------

const TABLE_HEAD = [
 { id: 'network', label: 'Network', alignRight: false },
 { id: 'name', label: 'Name', alignRight: false },
  { id: 'pin', label: 'PIN', alignRight: false },
  { id: 'serial', label: 'Serial Number', alignRight: false },
  { id: 'data_card_id', label: 'Data Card ID', alignRight: false },
  {id: 'buyer_username', label: 'Buyer Username', alignRight: false},
  {id: 'added_date', label: 'Added Date', alignRight: false},
  {id: 'bought_date', label: 'Bought Date', alignRight: false},
  {id: 'status', label: 'status', alignRight: false},
];
const ACCOUNT_TABS = [
    {
     value: 'ALL',
     label: 'ALL'
    },
    {
      value: 1,
      label: 'Sold'   
    },
    {
      value: 0,
     label: 'Available'
    },
  ];
// ----------------------------------------------------------------------

export default function StoreRechargeCardPlan() {
  const theme = useTheme();
  const { themeStretch } = useSettings();
  const [userList, setUserList] = useState([]);
  const [page, setPage] = useState(0);
const [load,SetLoad] = useState(true);
const [totalPage, SetTotal] = useState(0);
  const [filterName, setFilterName] = useState('');
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [dense, Setdense] = useState(false);
  const [selected, setSelected] = useState([]);
  const [isNotFound, SetNotFound] = useState(false);
  const [status, setStatus] = useState('ALL');
  const { enqueueSnackbar } = useSnackbar();
  const AccessToken =  window.localStorage.getItem('accessToken');

   useEffect(()  => {
    initialize(page,rowsPerPage,status,filterName);
}, []);

const initialize = async (pag,Habukhan = 5,status,search) => {
  let Habukhan_page;
  if(pag > page){
     Habukhan_page = pag + 1;
  }else{
     Habukhan_page = pag;
  }
      SetLoad(true);
      try{
  
const data =  await axios.get(`/api/all/store/plan/${AccessToken}/secure?page=${Habukhan_page}&Habukhan=${Habukhan}&status=${status}&search=${search}&database_name=store_recharge_card`)

 setUserList(data.data?.store_recharge_card.data);
 SetTotal(data.data?.store_recharge_card?.total)
SetLoad(false);       
setPage(pag);
SetNotFound(data.data?.store_recharge_card?.total === 0)
 SetLoad(false);

}catch(_){
SetLoad(false);
setUserList([])
} 
}

 


  const handleSelectAllClick = (checked) => {
    if (checked.target.checked) {
      const newSelecteds = userList.map((n) => n.id);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (username) => {
    const selectedIndex = selected.indexOf(username);
    let newSelected = [];
    if (selectedIndex === -1) {
        newSelected = newSelected.concat(selected, username);
      } else if (selectedIndex === 0) {
        newSelected = newSelected.concat(selected.slice(1));
      } else if (selectedIndex === selected.length - 1) {
        newSelected = newSelected.concat(selected.slice(0, -1));
      } else if (selectedIndex > 0) {
        newSelected = newSelected.concat(
          selected.slice(0, selectedIndex),
          selected.slice(selectedIndex + 1),
        );
      }
    setSelected(newSelected);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    initialize(0,event.target.value,status,filterName)
    setPage(0);
  };
  
  const handleFilterByName = async(filterName) => {
    setFilterName(filterName);
    setPage(0);
    if(filterName){
      initialize(page,rowsPerPage,status,filterName);
      }else{
      initialize(page,rowsPerPage,status,filterName);
      SetNotFound(false)
      SetLoad(false);
     }
  };

  const handleDeleteMultiUser = (selected) => {
    swal({
        title: "Note",
        text: `Are You Sure You Want To Delete plans`,
        icon: "info",
        buttons: true,
        dangerMode: true,
      })
      .then((willDelete) => {
        if (willDelete) {
            axios.post(`/api/delete/store_recharge_card/${AccessToken}/system`,{
                plan_id:selected
            }).then(data => {
                enqueueSnackbar(data.data.message)
    const deleteUsers = userList.filter((user) => !selected.includes(user.id));
    setSelected([]);
    setUserList(deleteUsers);  
            }).catch(error => {
                if(error.data?.message){
                    enqueueSnackbar(error.data.message, { variant: 'error' })   
                }else{
                    enqueueSnackbar('An Error Occured', { variant: 'error' })   
                }
            });
        } else {
            swal("Request Cancelled");
          }
        });
  };




  function handleChangeDense(event){
   Setdense(event.target.checked);
  }

  return (
    <Page title="Plans: Store Recharge Card">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Store Recharge Card"
          links={[
            { name: 'Dashboard', href: PATH_ADMIN.root },
            {name: 'Add Recharge Card Pin', href: PATH_ADMIN.plan.add_store_recharge_card},
            { name: 'List' },
          ]}
          action={
            <Button
              variant="contained"
              component={RouterLink}
              to={PATH_ADMIN.plan.add_store_recharge_card}
              startIcon={<Iconify icon={'eva:plus-fill'} />}
            >
              New Recharge Card Pin
            </Button>
          }
        />

        <Card>
        <Tabs
          value={status}
          scrollButtons="auto"
          variant="scrollable"
          allowScrollButtonsMobile
          onChange={(e, value) => {setStatus(value); initialize(page,rowsPerPage,value,filterName); }}
        >
          {ACCOUNT_TABS.map((tab) => (
            <Tab disableRipple key={tab.value} label={capitalCase(tab.label)} value={tab.value} />
          ))}
        </Tabs>
          <PlanToolbar
            numSelected={selected.length}
            filterName={filterName}
            onFilterName={handleFilterByName}
            onDeleteUsers={() => handleDeleteMultiUser(selected)}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table
              size={dense ? 'small' : 'medium'}
              >
                <UserListHead
                  // order={order}
                  // orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  rowCount={userList.length}
                  numSelected={selected.length}
                  // onRequestSort={handleRequestSort}
                  onSelectAllClick={handleSelectAllClick}
                  onDeleteUsers={() => handleDeleteMultiUser(selected)}
                />

               {!load ? <TableBody>
                  {userList.map((row) => {
                    const { name, network, serial, pin, bought_date, id, plan_status, added_date, buyer_username, recharge_card_id } = row;
                    const isItemSelected = selected.indexOf(id) !== -1;
                    return (
                      <TableRow
                        hover
                        key={id}
                        tabIndex={-1}
                        role="checkbox"
                        selected={isItemSelected}
                        aria-checked={isItemSelected}
                      >
                        <TableCell>
                          <Checkbox checked={isItemSelected} onClick={() => handleClick(id)} />
                        </TableCell>

                        <TableCell sx={{ display: 'flex', alignItems: 'center' }}>
                          <Typography variant="subtitle2" noWrap>
                            {network}
                          </Typography>
                        </TableCell>
                        <TableCell align="left">{name}</TableCell>
                        <TableCell align="left">{pin}</TableCell>
                        <TableCell align="left">{serial}</TableCell>
                        <TableCell align="left">{recharge_card_id}</TableCell>
                        <TableCell align="left">{buyer_username}</TableCell>
                        <TableCell align="left">{added_date}</TableCell>
                        <TableCell align="left">{bought_date}</TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(plan_status === 1 ? 'success' : 'warning')}
                          >
                            {sentenceCase((plan_status === 0 && 'available') || (plan_status === 1 && 'sold'))}
                          </Label>
                        </TableCell>

                        <TableCell align="right">
                          <PlanMoreMenu userName={id.toString()} route='edit_store_recharge_card' />
                        </TableCell>
                      </TableRow>
                    );
                  })}
             
                  
                </TableBody> : <TableRow>
                      <TableCell align="center" colSpan={5} sx={{ py: 3 }}>
                      <Typography variant="body2">Loading ...</Typography>
                      </TableCell>
                    </TableRow> }
                {isNotFound && (
                  
                    <TableRow>
                      <TableCell align="center" colSpan={5} sx={{ py: 3 }}>
                        <SearchNotFound searchQuery={filterName} />
                      </TableCell>
                    </TableRow>
                 
                )}
              </Table>
            </TableContainer>
          </Scrollbar>
          
          <FormControlLabel
        control={<Switch checked={dense} onChange={(event) => handleChangeDense(event)} />}
        label="Dense padding" align="left"
      />   
         
         <TablePagination
            rowsPerPageOptions={[5, 10, 25,  100]}
            component="div"
            count={totalPage}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={(e, page) =>  initialize(page,rowsPerPage,status,filterName)}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
            
        </Card>
       
      </Container>
    </Page>
  );
}

// ----------------------------------------------------------------------

